import {
  Layout,
  Typography,
  Menu,
  Badge,
  Divider,
  Dropdown,
  Input,
} from "antd";
import {
  UserOutlined,
  BellTwoTone,
  MessageTwoTone,
  SettingOutlined,
  LogoutOutlined,
} from "@ant-design/icons";
import css from "../assets/General.css";
import React from "react";

const { Header } = Layout;
const { Search } = Input;

class Navbar extends React.Component {
  constructor(props) {
    super(props);
  }
  state = {
    visible: false,
    placement: "left",
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    function handleMenuClick(e) {
      console.log("click", e);
    }

    const menu = (
      <Menu onClick={handleMenuClick}>
        <Menu.Item>
          <SettingOutlined />
          Profil{" "}
        </Menu.Item>
        <Menu.Item>
          <LogoutOutlined />
          Logout
        </Menu.Item>
      </Menu>
    );
    return (
      <div>
        <head>
          <title>Brain Project</title>
          <link
            ref="shortcut icon"
            type="image/x-icon"
            src="../assets/PG.png"
          />
        </head>
        <Layout>
          <Header className="site-layout-background" className={css.header}>
            <Search style={{ width: 400 }} placeholder="Search" />
            <Divider type="vertical" style={{ fontSize: 30 }} />
            <Badge dot={false}>
              <MessageTwoTone
                twoToneColor="#1890ff"
                style={{ fontSize: 20, paddingLeft: 610 }}
              />
            </Badge>
            <Badge count={5} dot={false}>
              <BellTwoTone
                twoToneColor="#eb2f96"
                style={{ fontSize: 20, paddingLeft: 10 }}
              />
            </Badge>
            <Dropdown.Button
              style={{ paddingLeft: 20 }}
              overlay={menu}
              icon={<UserOutlined />}
            >
              Account
            </Dropdown.Button>
          </Header>
        </Layout>
      </div>
    );
  }
}

export default Navbar;
