import React from "react";
import { useEffect, useState } from "react";
import { EllipsisOutlined } from "@ant-design/icons";
import {
  Descriptions,
  Menu,
  Layout,
  Card,
  Row,
  Col,
  List,
  Avatar,
  Typography,
  Button,
  Select,
} from "antd";

import "../../assets/General.css";
import Navbar from "../../component/Navbar";
import Footer from "../../component/Footer";
import http from "../../services/api";
import Sidebar from "../../component/Sidebar";
import MyLayout from "../../component/MyLayout";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
const { Content, Sider } = Layout;
const { SubMenu } = Menu;
const { Text } = Typography;

function Timesheets(props) {
  // const [projects, setProjects] = useState([]);
  const [collapsed, setCollapsed] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isGitlabPLoaded, setIsGitlabPLoaded] = useState(false);
  const [gProjects, setGProjects] = useState([]);
  const [gitlabId, setGitlabId] = useState("");
  const { Option } = Select;
  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };

  const { id } = useParams();

  const [timesheets, setTimesheets] = useState([]);
  const [issues, setIssues] = useState([]);
  const [project, setProjects] = useState([]);

  useEffect(() => {
    http.get("/resources/projects/" + id).then((res) => {
      setProjects(res);
    });

    const data = async () => {
      const project = await http.get("resources/projects/" + id);
      const issues = await http.get(
        "gitlab/projects/" + project.gitlabId + "/issues"
      );
      setIssues(issues);
      const timesheets = await http.post(
        "bexio/timesheets/" + project.bexioId
      );
      setTimesheets(timesheets);
    };

    data();
  }, []);

  return (
    <>
      <Layout>
        <Sider
          trigger={null}
          collapsible
          collapsed={collapsed}
          className="sidebarShadow"
        >
          <Sidebar toggleCollapsed={toggleCollapsed} />
        </Sider>

        <Layout>
          <MyLayout>
            <Navbar />
            <Content
              className="site-layout-background"
              style={{
                margin: "24px 16px",
                padding: 24,
                minHeight: 280,
              }}
            >
              <Card className="cardForm">
                <div className="site-card-wrapper">
                  <Row gutter={16}>
                    <Col span={12}>
                      <Card title="Bexio timesheets" bordered={false}>
                        <List
                          itemLayout="horizontal"
                          dataSource={timesheets}
                          renderItem={(item) => (
                            <List.Item>
                              <List.Item.Meta
                                avatar={
                                  <Avatar src="https://media.glassdoor.com/sql/1312614/bexio-squarelogo-1500988647798.png" />
                                }
                                title={
                                  <>
                                    <a>#{item.id}</a> - Duration:{" "}
                                    {item.duration}h
                                  </>
                                }
                                description={item.text}
                              />
                            </List.Item>
                          )}
                        />
                      </Card>
                    </Col>
                    <Col span={12}>
                      <Card title="Gitlab timesheets" bordered={false}>
                        <List
                          itemLayout="horizontal"
                          dataSource={issues}
                          renderItem={(item) => (
                            <List.Item>
                              <List.Item.Meta
                                avatar={
                                  <Avatar src="https://about.gitlab.com/images/press/logo/png/gitlab-icon-rgb.png" />
                                }
                                title={
                                  <>
                                    <a href={item.web_url}>
                                      #{item.iid}
                                      {item.title}
                                    </a>{" "}
                                    <span>
                                      - Estimate:{" "}
                                      {item.time_stats.human_time_estimate || 0}{" "}
                                      / Spent:{" "}
                                      {item.time_stats.human_total_time_spent || 0}
                                    </span>
                                  </>
                                }
                                description={item.description}
                              />
                            </List.Item>
                          )}
                        />
                      </Card>
                    </Col>
                  </Row>
                  <Row>
                    <Col
                      style={{
                        marginTop: "10px",
                        textAlign: "right",
                        float: "right",
                      }}
                      span={24}
                    >
                      <Button>Back</Button>{" "}
                      <Button type="primary">Submit</Button>
                    </Col>
                  </Row>
                </div>
              </Card>
            </Content>
          </MyLayout>
        </Layout>
      </Layout>
    </>
  );
}

export default Timesheets;
