import React from "react";
import { useEffect, useState } from "react";
import { EllipsisOutlined } from "@ant-design/icons";
import {
  Descriptions,
  Menu,
  Layout,
  Card,
  Modal,
  Typography,
  Button,
  Select,
} from "antd";

import "../../assets/General.css";
import Navbar from "../../component/Navbar";
import Footer from "../../component/Footer";
import http from "../../services/api";
import Sidebar from "../../component/Sidebar";
import MyLayout from "../../component/MyLayout";
import { Link } from "react-router-dom";
import { useParams } from "react-router-dom";
const { Content, Sider } = Layout;
const { SubMenu } = Menu;
const { Text } = Typography;
function ProjectDetails() {
  //const [projects, setProjects] = useState([]);
  const [collapsed, setCollapsed] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isGitlabPLoaded, setIsGitlabPLoaded] = useState(false);
  const [gProjects, setGProjects] = useState([]);
  const [project, setProject] = useState([]);
  const [gitlabId, setGitlabId] = useState("");
  const { Option } = Select;
  const { id } = useParams();

  useEffect(() => {
    http.get("/resources/projects/" + id).then((res) => {
      setProject(res);
    });
  }, []);

  const onChange = (value) => {
    console.log(`selected ${value}`);
  };
  const onSearch = (value) => {
    console.log(`selected ${value}`);
  };

  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };

  const openModal = () => {
    setIsModalOpen(true);
    setIsGitlabPLoaded(false);

    http.get("/gitlab/projects").then((res) => {
      console.log(gProjects);
      setIsGitlabPLoaded(true);
      setGProjects(res);
    });
  };
  const closeModal = () => {
    setIsModalOpen(false);
  };

  const handleModal = () => {
    http
      .put("/resources/projects/" + id, { gitlabId })
      .then((res) => {
        setIsModalOpen(false);
        setGProjects(res);
      })
      .catch((er) => console.log(er));
  };

  // useEffect(() => {
  //   http.get("api/projects/" + id).then((res) => {
  //     setProjects(res);
  //   });
  // }, [collapsed]);

  return (
    <>
      <Layout>
        <Sider
          trigger={null}
          collapsible
          collapsed={collapsed}
          className="sidebarShadow"
        >
          <Sidebar toggleCollapsed={toggleCollapsed} />
        </Sider>

        <Layout>
          <MyLayout>
            <Navbar />
            <Content
              className="site-layout-background"
              style={{
                margin: "24px 16px",
                padding: 24,
                minHeight: 280,
              }}
            >
              <Text className="title" strong>
                Project: {project.name}
              </Text>
              <Card
                className="cardForm"
                actions={[
                  <Button
                    disabled={project.gitlabId != null}
                    onClick={openModal}
                    type="primary"
                    shape="round"
                  >
                    Add to Gitlab
                  </Button>,
                  <Button
                    disabled={project.gitlabId == null}
                    type="secondary"
                    shape="round"
                  >
                    <Link to={`/timesheets/${project._id}`}>
                      <a>Timesheets</a>
                    </Link>
                  </Button>,
                  <EllipsisOutlined key="ellipsis" />,
                ]}
              >
                <Descriptions className="font" bordered>
                  <Descriptions.Item label="Name : ">
                    {project.name}
                  </Descriptions.Item>
                  <Descriptions.Item label="Description : ">
                    {project.comment}
                  </Descriptions.Item>
                  <Descriptions.Item label="Start Date : ">
                    {project.start_date}
                  </Descriptions.Item>
                  <Descriptions.Item label="End date : ">
                    {project.end_date}
                  </Descriptions.Item>
                  <Descriptions.Item label="Contact sub : ">
                    {project.contact_sub_id}
                  </Descriptions.Item>
                  <Descriptions.Item label="Invoice type : ">
                    {project.pr_invoice_type_id}
                  </Descriptions.Item>
                  <Descriptions.Item label="Invoice amount : " span={1}>
                    {project.pr_invoice_type_amount}
                  </Descriptions.Item>
                  <Descriptions.Item label="Budget type : ">
                    {project.pr_budget_type_id}
                  </Descriptions.Item>
                  <Descriptions.Item label="Budget amount : ">
                    {project.pr_budget_type_amount}
                  </Descriptions.Item>
                </Descriptions>
              </Card>
            </Content>
          </MyLayout>
        </Layout>
      </Layout>

      <Modal
        title="Choose a gitlab project"
        visible={isModalOpen}
        onOk={handleModal}
        onCancel={closeModal}
      >
        <Select
          showSearch
          style={{ width: 200 }}
          placeholder="Select a project"
          optionFilterProp="children"
          onChange={setGitlabId}
          onSearch={onSearch}
          loading={!isGitlabPLoaded}
        >
          {setGitlabId}
          {gProjects.map((gp) => {
            return (
              <Option key={gp.id} value={gp.id}>
                {gp.name_with_namespace}
              </Option>
            );
          })}
        </Select>
      </Modal>
    </>
  );
}

export default ProjectDetails;
