import {
  Table,
  Button,
  Typography,
  Row,
  Col,
  Layout,
  Divider,
  Popconfirm,
} from "antd";
import Highlighter from "react-highlight-words";
import { Link } from "react-router-dom";
import MyLayaout from "../../component/MyLayout";
import React, { useEffect, useState, useCallback } from "react";
import { DeleteOutlined, PlusCircleOutlined } from "@ant-design/icons";
import css from "../../assets/General.css";
import http from "../../services/api";
import Navbar from "../../component/Navbar";
import Sidebar from "../../component/Sidebar";

const { Content, Sider } = Layout;
const { Text } = Typography;

function Project() {
  const [projects, setProjects] = useState([]);
  const [collapsed, setCollapsed] = useState(false);
  const [loading, setLoading] = useState(false);

  const toggleCollapsed = () => {
    setCollapsed(!collapsed);
  };
  const handleDelete = () => { };

  const handleSync = async () => {
    setLoading(true);
    const data = await http.get("/bexio/projects/sync");
    setProjects(data);
    setLoading(false);
  }

  useEffect(() => {
    http.get("/resources/projects").then((res) => {
      setProjects(res);
    });
  }, [collapsed]);

  const columns = [
    {
      title: "NumberId",
      dataIndex: "nr",
      key: "nr",
      render: (text, record) => (
        <Link to={`/project/${record._id}`}>
          <a>{text}</a>
        </Link>
      ),
      //...this.getColumnSearchProps('nr'),
      defaultSortOrder: "descend",
      sorter: (a, b) => a.nr - b.nr,
    },
    {
      title: "Name",
      dataIndex: "name",
      key: "name",
      render: (text) => <span>{text}</span>,
      //...this.getColumnSearchProps('name'),
      filterMultiple: false,
      onFilter: (value, record) => record.name.indexOf(value) === 0,
      sorter: (a, b) => a.name.length - b.name.length,
      defaultSortOrder: "ascend",
    },
    {
      title: "Start date",
      dataIndex: "start_date",
      key: "start_date",
      render: (text) => <span>{text}</span>,
      //...this.getColumnSearchProps('name'),
      filterMultiple: false,
      onFilter: (value, record) => record.start_date.indexOf(value) === 0,
      sorter: (a, b) => a.start_date > b.start_date,
      defaultSortOrder: "ascend",
    },
    {
      title: "End date",
      dataIndex: "end_date",
      key: "end_date",
      render: (text) => <span>{text || "N/A"}</span>,
      //...this.getColumnSearchProps('name'),
      filterMultiple: false,
      onFilter: (value, record) => record.end_date.indexOf(value) === 0,
      sorter: (a, b) => a.end_date > b.end_date,
      defaultSortOrder: "ascend",
    },
    {
      title: "Action",
      key: "action",
      render: (text, record) => (
        <span>
          <Popconfirm
            title="Sure to delete?"
            onConfirm={() => handleDelete(record._id)}
          >
            <Button className={css.button} type="primary" shape="round">
              <DeleteOutlined />
              Delete
            </Button>
          </Popconfirm>
          <Divider type="vertical" />
          <Link to={`/project/${record._id}`}>
            <Button type="primary" shape="round">
              <PlusCircleOutlined />
              Details
            </Button>
          </Link>
        </span>
      ),
    },
  ];

  return (
    <>
      <Layout>
        <Sider
          trigger={null}
          collapsible
          collapsed={collapsed}
          className={css.sidebarShadow}
        >
          <Sidebar toggleCollapsed={toggleCollapsed} />
        </Sider>
        <Layout>
          <MyLayaout>
            <Navbar />
            <Content
              className="site-layout-background"
              style={{
                margin: "24px 16px",
                padding: 24,
                minHeight: 280,
              }}
            >
              <Row>
                <Text style={{ fontSize: 20 }} strong>
                  Projects
                </Text>
                <Col span={24}>
                  <div className={css.table}>
                    <Button
                      style={{
                        margin: "5px",
                      }}
                      type="primary"
                      shape="round"
                      loading={loading}
                      onClick={handleSync}
                    >
                      Sync.
                    </Button>
                    <Table columns={columns} dataSource={projects} />
                  </div>
                </Col>
              </Row>
            </Content>
          </MyLayaout>
        </Layout>
      </Layout>
    </>
  );
}
export default Project;
